<template>
  <div>
    <div id="mescroll" class="mescroll">
      <div class="home">
        <home-header :userName="getData" :comData='comData'></home-header>
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(item,index) in banner" :key="item.id">
            <img v-lazy="URL + item.pic_url" @click="toGoods(item.gid)">
          </mt-swipe-item>
        </mt-swipe>
        <div class="hotGoods-wrap">
          <!-- <router-link to="groupList">点这里</router-link> -->
          <ul class="nav-list clearfix">
            <li :class="{isSelected:index == isClass}" v-for="(item,index) in tabbar.font" :key="item.id" @click="tabswitch(index)">
              <span class="icon">
                    <img :src="tabbar.Icon[index]">
                </span>
              <span class="icon active">
                    <img :src="tabbar.isIcon[index]">
                </span>
              <span>{{item}}</span>
            </li>
          </ul>
          <div class="sold" v-if="stime">
            <div class="sold_time" ref="soldNum">
              <ul class="clearfix" :style="{width:(stime.length)*1.75+'rem'}">
                <li class="fl" v-for="(item,index) in stime" :key="item.id" :class="{active:item.id == flag}" @click="tab(item,index)">
                  <!-- <span>{{date}}</span> -->
                  <span v-if="item.starttime < nowhour">已开抢</span>
                  <span v-if="item.starttime == nowhour">最新抢购</span>
                  <span v-if="item.starttime > nowhour">未开抢</span>
                  <p class="time_interval">{{item.name}}</p>
                </li>
              </ul>
            </div>
          </div>
          <p v-if="stime" class="countdown">{{title}}：
            <!-- <span>{{day||'00'}}</span> -->
            <!-- <i>天</i>/ -->
            <span>{{hour||'00'}}</span>
            <i>时</i>
            <span>{{Minute||'00'}}</span>
            <i>分</i>
            <span>{{second||'00'}}</span>
            <i>秒</i></p>
          <ul class="content-main clearfix">
            <li class="fl" v-for="(item,index) in data" :key="item.id">
              <div class="image" @click="tolink(index)">
                <img v-if="item.pic_url" v-lazy="URL + item.pic_url">
                <img v-if="item.stock == 0" class="sellout_img" src="../../assets/sellout2.jpg" alt="">
              </div>
              <div>
                <p class="desi"><span v-if="item.goods_marking == i" class="mark" v-for="(m,i) in mark" :key="m.id">{{m}}</span>{{item.title}}</p>
              </div>
              <p class="stock" v-if="item.total_stock!=null&item.total_stock!=0">
                <span class="fl already_bought">已抢 {{percentage(item)}}%</span>
                <span class="fr seckill_items_reserve_bar">
                        <em :style="{width:percentage(item)+'%'}"></em>
                    </span>
              </p>
              <div class="price_market clearfix">
                <p v-if="item.total_stock == null || item.total_stock == 0" class="surplus_stock">库存：{{item.stock}}</p>
                <p class="price text-single-hidden fl">￥<span>{{item.price_member}}</span></p>
                <strike class="market text-single-hidden fl">￥{{item.price_market}}</strike>
                <!-- <span class="cart fr" @click="join(item)"></span> -->
                <span v-if="item.is_customs == 0" class="cart fr" @click="join(item)"></span>
              </div>
            </li>
          </ul>
          <!-- <div class="no_goods" v-show="show">暂无商品</div> -->
        </div>
        <div class="load-wrap" v-show="$store.state.load_wrap" @touchmove.prevent>
          <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
        </div>
      </div>
      <div v-show="$store.state.show_download" style="width:100%;height:100%;position: fixed;left:0;top:0;">
        <down-load></down-load>
      </div>
    </div>
    <foot-nav></foot-nav>
  </div>
</template>

<script>
  import footNav from '../footer/footer.vue'
  import HomeHeader from './children/Header';
  import downLoad from './children/children/downLoad'
  import {
    Toast
  } from 'mint-ui';
  import QS from 'qs';
  export default {
    name: 'home',
    data() {
      return {
        title: '距当前秒杀时间',
        mark: {},
        day: '',
        hour: '',
        Minute: '',
        second: '',
        timer: null,
        flag: null,
        isClass: 1,
        tabbar: {
          isIcon: [require('@/assets/nav-005.png'), require('@/assets/nav-004.png'), require('@/assets/nav-006.png')],
          Icon: [require('@/assets/nav-002.png'), require('@/assets/nav-001.png'), require('@/assets/nav-003.png')],
          font: ['明日预告', '今日秒抢', '秒抢尾单'],
        },
        date: '',
        toTopImage: require('@/assets/totop-1.png'),
        continuous: true,
        defaultIndex: 0,
        showIndicators: true,
        prevent: true,
        stopPropagation: true,
        stime: '',
        status: [true, false],
        load: false,
        data: [],
        banner: '',
        comData:{},
        show: false,
        version: '',
        app_type: '',
        today_status: 1,
        count: 0,
        time_slot_id: '',
        time_status: false,
        page: {},
        limit: false,
        nowhour: '',
  
      }
    },
    beforeRouteLeave(to, from, next) {
      this.$store.state.changeHomeScrollY = 0;
      this.$store.state.groupScrollY = 0;
      this.$store.state.groupData = [];
      sessionStorage.removeItem('classId');
      next();
    },
    computed:{
      //进度条
      percentage(item) {
          return function(item){
            return ((parseInt(item.total_stock) - parseInt(item.stock))/parseInt(item.total_stock)*100).toString().split('.')[0];
        }
      }
    },
    methods: {
      
      getMark() {
        this.axios({
          url: API_URL + 'Home/Index/getGoodsMarking',
          method: 'get',
        }).then((res) => {
          this.mark = res.data.data;
        }).catch((err) => {
          console.log(err);
        });
      },
      callback() {
        console.log('已经结束了！')
      },
      //计算倒计时或已开抢时间
      computationTime(now_Time, timestamp) {
        let self = this;
        this.timer = setInterval(function() {
          let t = null;
          let now = new Date(now_Time * 1000);
          let endTime = new Date(timestamp * 1000);
          // console.log(now)
          // console.log(endTime)
          if (now > endTime) {
            t = now.getTime() - endTime.getTime();
          } else {
            t = endTime.getTime() - now.getTime();
          }
          if (t > 0) {
            let day = Math.floor(t / 86400000);
            let hour = Math.floor(t / 3600000);
            let min = Math.floor((t / 60000) % 60);
            let sec = Math.floor((t / 1000) % 60);
            day = day < 10 ? "0" + day : day;
            hour = hour < 10 ? "0" + hour : hour;
            min = min < 10 ? "0" + min : min;
            sec = sec < 10 ? "0" + sec : sec;
            self.day = `${day}`;
            self.hour = `${hour}`;
            self.Minute = `${min}`;
            self.second = `${sec}`;
          } else {
            clearInterval(self.timer);
            self.day = `00`;
            self.hour = `00`;
            self.Minute = `00`;
            self.second = `00`;
          }
          if (now > endTime) {
            self.title = '已开抢时间';
          } else {
            self.title = '距当前秒杀时间';
          }
          now_Time += 1;
        }, 1000);
      },
      //导航切换
      tabswitch(index) {
        if (this.limit === true) {
          return
        };
        this.data = [];
        this.time_status = false;
        this.isClass = index;
        this.mescroll.resetUpScroll(); //重置列表为第一页
        this.status = [true, false];
      },
      //获取明天商品数据;
      getTomorrowData(page) {
        clearInterval(this.timer);
        if (this.limit === true) {
          return
        };
        this.limit = true;
        this.today_status = 2;
        this.axios.post(API_URL + 'Home/Index/tomorrow', QS.stringify({
          app_user_id: sessionStorage.getItem('user_ID')
        })).then((res) => {
          if (res.data.status === 1) {
            this.date = res.data.data.date;
            this.limit = false;
            this.flag = res.data.data.flag;
            this.data = this.data.concat(res.data.data.tomorrow_clear.goods);
            this.computationTime(res.data.data.time, res.data.data.starttime);
            if (this.data.length === 0) {
              this.mescroll.endUpScroll()
            };
            this.count = res.data.data.tomorrow_clear.count;
            this.stime = res.data.data.soldname;
            this.mescroll.endBySize(res.data.data.tomorrow_clear.goods.length, this.count);
          } else {
            this.mescroll.endErr();
          }
  
        }).catch((err) => {
          console.log(err);
        })
      },
      //判断是否要重新登录
      checkLogoin(time) {
        let ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          let locoltime = localStorage.getItem('logoin_time_code');
          if(!locoltime){
            locoltime = 0;
          }
          if (locoltime < time) {
            // this.flagLogin = true;
            this.$router.push({
              path: '/LogoIn',
              query: {
                login_time: 'y',
              }
            });
          }
        }
  
  
      },
      //获取今天商品数据
      getTodayData(page) {
        clearInterval(this.timer);
        if (this.limit === true) {
          return
        };
        this.limit = true;
        this.today_status = 1;
        this.axios.post(API_URL + 'Home/Index/home', QS.stringify({
          app_user_id: sessionStorage.getItem('user_ID'),
          page: page.num
        })).then((res) => {
          setTimeout(() => {
            this.mescroll.endSuccess(); //下拉刷新结束动画
          }, 200)
          if (res.data.status == 1) {
            this.checkLogoin(res.data.data.logoin_time);
            this.limit = false;
            this.flag = res.data.data.flag;
            this.date = res.data.data.date;
            console.log(this.date,'this.date11111');
            this.nowhour = res.data.data.hour;
            this.comData=res.data.data;
            this.computationTime(res.data.data.time, res.data.data.starttime);
            this.$store.state.load_wrap = false;
            this.banner = res.data.data.banner;
            this.data = this.data.concat(res.data.data.today_clear.goods);
            if (this.data.length == 0) {
              this.mescroll.endUpScroll()
            };
            this.count = res.data.data.today_clear.count;
            this.stime = res.data.data.soldname;
            this.mescroll.endBySize(res.data.data.today_clear.goods.length, this.count);
            } else if(res.data.status ==99){
            sessionStorage.removeItem('user_ID');
            sessionStorage.removeItem('set_address');
            localStorage.removeItem('user_ID');
            this.$router.push('/LogoIn');
          } else {
            this.mescroll.endErr();
          }
          this.$nextTick(function(){
              if(this.flag > 4){this.$refs.soldNum.scrollLeft = 262};
            })
        }).catch((err) => {
          console.log(err);
        });
      },
      //获取昨日商品数据
      getYesterdayData(page) {
        clearInterval(this.timer);
        if (this.limit === true) {
          return
        };
        this.limit = true;
        this.today_status = 3;
        this.axios.post(API_URL + 'Home/Index/yesterday', QS.stringify({
          app_user_id: sessionStorage.getItem('user_ID'),
          page: page.num
        })).then((res) => {
          if (res.data.status == 1) {
            this.limit = false;
            this.$store.state.load_wrap = false;
            this.data = this.data.concat(res.data.data.yesterday_clear.goods);
            if (this.data.length == 0) {
              this.mescroll.endUpScroll()
            };
            this.count = res.data.data.yesterday_clear.count;
            this.stime = '';
            this.mescroll.endBySize(res.data.data.yesterday_clear.goods.length, this.count);
          } else {
            this.mescroll.endErr();
          }
  
        }).catch((err) => {
          console.log(err);
        })
      },
      // 点击时间段获取商品
      tab(item, index) {
        if (this.load === true) {
          return
        };
        this.data = [];
        this.flag = item.id;
        this.load = true;
        this.time_slot_id = item.id;
        this.time_status = true;
        this.starttime = item.starttime;
        this.mescroll.resetUpScroll() //重置列表为第一页并自动调用一次
      },
      succ(id, page) {
        let data = '';
        if (this.today_status === 1) {
          data = {
            cid: 1,
            page: page.num,
            sid: id,
            app_user_id: sessionStorage.getItem('user_ID')
          }
        } else if (this.today_status === 2) {
          data = {
            cid: 3,
            page: page.num,
            sid: id,
            app_user_id: sessionStorage.getItem('user_ID')
          }
        } else {
          return false;
        }
        this.axios({
          url: API_URL + 'Home/Index/getSgoods',
          method: 'get',
          params: data
        }).then((suc) => {
          if (suc.data.status === 1) {
            this.load = false;
            clearInterval(this.timer);
            this.computationTime(suc.data.data.time, this.starttime);
            this.data = this.data.concat(suc.data.data.goods);
            this.count = suc.data.data.count;
            if (this.data.length === 0) {
              this.mescroll.endUpScroll()
            };
            this.mescroll.endBySize(suc.data.data.goods.length, this.count);
          } else {
            this.mescroll.endErr();
          }
  
        }).catch((err) => {
          console.log(err);
        });
      },
      //加入购物车
      join(item) {
        if (!sessionStorage.getItem('user_ID')) {
          this.$router.push('/LogoIn');
          return;
        } else if (item.stock === 0) {
          Toast({
            message: '库存不足',
            position: 'middle',
            duration: 800
          });
          return;
        };
        this.axios.post(API_URL + 'Home/Cart/add_cart', QS.stringify({
          app_user_id: sessionStorage.getItem('user_ID'),
          goods_id: item.id,
          goods_num: 1,
          price_new: item.price_member
        })).then((res) => {
          if (res.data.status === 1) {
            Toast({
              message: res.data.msg,
              position: 'middle',
              duration: 800
            });
          } else {
            Toast({
              message: res.data.msg,
              position: 'middle',
              duration: 800
            });
          }
        }).catch((err) => {
          console.log(err);
        });
      },
      // 商品详情
      tolink(index) {
        this.$router.push({
          name: 'product',
          params: {
            id: this.data[index].id,
            status: 1
          }
        });
      },
      // 广告跳转
      toGoods(gid) {
        if (gid) {
          this.$router.push({
            name: 'product',
            params: {
              id: gid,
              status: 1
            }
          });
        }
  
      },
      upCallback: function(page) {
        if (this.time_status === true) {
          this.succ(this.time_slot_id, this.page);
        } else {
          this.page = page;
          switch (this.isClass) {
            case 0:
              this.getTomorrowData(page);
              break;
            case 1:
              this.getTodayData(page);
              break;
            case 2:
              this.getYesterdayData(page);
              break;
          }
        }
      },
      downCallback() {
        let _this = this;
        this.data = [];
        this.getMark();
        this.getTodayData({
          num: 1
        });
        this.isClass = 1;
      },
    },
  
    created() {
      // this.getMark();
      //是否直接登录
      if (!sessionStorage.getItem('user_ID')) {
        let userid = localStorage.getItem('user_ID');
        if (userid) {
          sessionStorage.setItem('user_ID', userid);
        } else {
          // this.$router.push('/LogoIn');return false;
        }
      }
  
      //测试app版本号
      if (this.client_type == 2) {
  
        var u = navigator.userAgent,
          app = navigator.appVersion;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
        // var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        if (isAndroid) {
          this.version = this.andr_version;
          this.app_type = 1;
        } else {
          this.version = this.ios_version;
          this.app_type = 2;
        }
        this.axios.post(API_URL + 'Home/Index/getVersion', QS.stringify({
          version: this.version,
          type: this.app_type
        })).then((res) => {
          if (res.data.status != 1) {
            if (this.app_type == 1) {
              Toast('请到官网更新至最新版本,可能需要进入手机“文件管理”找到APP并安装');
              window.open("http://www.2tianqc.com/App/2tianqc_a.apk");
            } else {
              Toast('请到苹果App Store搜索ShopsN');
            }
  
          }
        }).catch((err) => {
          console.log(err);
        });
      }
      //页面更新号
      if (this.client_type == 1) {
        this.version = this.andr_version;
        this.app_type = 1;
        
        this.axios.post(API_URL + 'Home/Index/getVersion', QS.stringify({
          version: this.version,
          type: this.app_type
        })).then((res) => {
          if (res.data.status != 1) {
            Toast('刷新');
            this.$router.go(0);
  
          }
        }).catch((err) => {
          console.log(err);
        });
      }
  
      
      this.getData = this.$route.params.userName;
    },
    mounted() {
      // document.body.scrollTop = 0;
      //创建MeScroll对象
      var self = this;
      self.mescroll = new MeScroll("mescroll", {
        up: {
          callback: self.upCallback, //上拉回调
          //以下参数可删除,不配置
          page: {
            size: 5
          }, //可配置每页8条数据,默认10
          toTop: {
            //配置回到顶部按钮
            warpId: "mescroll",
            src: self.toTopImage, //默认滚动到1000px显示,可配置offset修改
            offset: 1000,
            hideClass: "mescroll-fade-out"
          },
          //去除滚动条
          scrollbar: {
            use: false
          },
          //列表数据过少,不足以滑动触发上拉加载,是否自动加载下一页,直到满屏或无数据
          loadFull: {
            use: false,
            delay: 500
          },
          empty: {
            //配置列表无任何数据的提示
            warpId: "mescroll",
            tip: "暂无相关数据~"
            // icon: "../res/img/mescroll-empty.png"
  
          }
  
        },
        //下拉刷新
        down: {
          callback: self.downCallback, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
          use: self.down, //是否初始化下拉刷新; 默认true
          auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
          autoShowLoading: false, //如果在初始化完毕之后自动执行下拉回调,是否显示下拉刷新进度; 默认false
          isLock: false, //是否锁定下拉,默认false;
          isBoth: false, //下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认false,两者不可同时触发;
          offset: 60, //触发刷新的距离,默认80
          outOffsetRate: 0.2, //超过指定距离范围外时,改变下拉区域高度比例;值小于1且越接近0,越往下拉高度变化越小;
          bottomOffset: 20, //当手指touchmove位置在距离body底部20px范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
          minAngle: 45, //向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
          hardwareClass: "mescroll-hardware", //硬件加速样式;解决iOS下拉因隐藏进度条而闪屏的问题,参见mescroll.css
          warpId: null, //可配置下拉刷新的布局添加到指定id的div;默认不配置,默认添加到mescrollId
          warpClass: "mescroll-downwarp", //容器,装载布局内容,参见mescroll.css
          resetClass: "mescroll-downwarp-reset", //高度重置的动画,参见mescroll.css
        },
      });
    },
    components: {
      HomeHeader,
      downLoad,
      footNav
    }
  }
</script>

<style lang="less">
  .home {
    .mint-swipe-indicators {
      width: 100%;
      text-align: center;
    }
  }
</style>

<style lang="less" scoped>
