        li {
          width: 33.33%;
          float: left;
          line-height: .8rem;
          height: .8rem;
          .icon {
            display: inline-block;
            img {
              vertical-align: middle;
              display: inline-block;
              margin-bottom: .1rem;
              width: .45rem;
              height: .45rem;
            }
          }
          .icon.active {
            display: none;
          }
          span {
            font-size: .3rem;
            color: #464646;
            font-weight: 600;
          }
        }
        li.isSelected {
          .font {
            color: #ed3851;
          }
          .icon.active {
            display: inline-block;
          }
          .icon {
            display: none;
          }
          span {
            color: #ed3851;
          }
        }
      }
      .sold {
        height: 1rem;
        overflow: hidden;
        .sold_time {
          background: #fff;
          width: 100%;
          height: 1.7rem;
          overflow: hidden;
          overflow-x: auto;
          ul {
            height: 1.6rem;
            margin-top: .2rem;
            li {
              text-align: center;
              width: 1.45rem;
              height: 1rem;
              // line-height:.5rem;
              padding: 0 .15rem;
              color: #464646;
              span {
                font-size: .24rem;
                font-weight: 500;
              }
              @font-face {
                font-family: "ITCAvantGardeGothicLTBold";
                src: url('../../../static/fonts/myfont.ttf');
              }
              .time_interval {
                font-family: 'ITCAvantGardeGothicLTBold';
                font-size: .4rem;
                font-weight: 500;
                color: #464646;
              }
            }
            li.active {
              color: #ea2f2f;
              .time_interval{
                color: #ea2f2f;
              }
            }
          }
        }
      }
      .countdown {
        background: #fff;
        text-align: right;
        padding: .2rem;
        color: #787878;
        font-size: .2rem;
        i {
          font-style: normal;
          font-size: .2rem;
          display: inline-block;
        }
      }
      .content-main {
        background: #fff;
        li {
          width: 3.7rem;
          background: #fff;
          margin-top: .1rem;
          margin-right: .08rem;
          .stock {
            padding: 0 .2rem .2rem;
            font-size: .24rem;
            color: #aeadad;
            overflow: hidden;
            .already_bought {
              font-size: .24rem;
            }
            .seckill_items_reserve_bar {
              width: 1.8rem;
              margin-top: 2px;
              background-color: #ffe5e8;
              border-radius: 12px;
              overflow: hidden;
              position: relative;
              em {
                background-color: #fb5c71;
                height: 6px;
                display: block;
              }
            }
            .seckill_items_reserve_bar::after {
              content: "";
              display: block;
              border: 1px solid #fb5c71;
              border-radius: 12px;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              pointer-events: none;
            }
            @media only screen and (-webkit-min-device-pixel-ratio: 2) {
              .seckill_items_reserve_bar::after {
                right: -100%;
                bottom: -100%;
                -webkit-transform: scale(.5);
                -webkit-transform-origin: 0 0;
              }
            }
          }
          .image {
            width: 3.7rem;
            height: 3.7rem;
            position: relative;
            margin-bottom: .2rem;
            border-radius: .15rem;
            img {
              // border-radius:.15rem;
              width: 100%;
              height: 100%;
            }
            .sellout_img {
              position: absolute;
              width: 2.5rem;
              height: 2.5rem;
              top: 17%;
              left: 17%;
            }
          }
          .desi {
            font-family: 'NotoSansHans-Regular';
            line-height: .35rem;
            font-weight: 400;
            font-size: .26rem;
            height: .7rem;
            color: #333;
            padding: 0 .2rem;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            .mark {
              position: relative;
              padding: 0 .08rem;
              display: inline-block;
              color: #f81919;
              line-height: normal;
              font-size: .24rem;
            }
            .mark::after {
              content: "";
              display: block;
              border: 1px solid #f81919;
              border-radius: 25px;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              pointer-events: none;
            }
            @media only screen and (-webkit-min-device-pixel-ratio: 2) {
              .mark::after {
                right: -100%;
                bottom: -100%;
                -webkit-transform: scale(.5);
                -webkit-transform-origin: 0 0;
              }
            }
          }
          .price_market {
            padding: .2rem;
            padding-top: 0;
            position: relative;
            .surplus_stock {
              font-size: .24rem;
              color: #aeadad;
              padding-top: .2rem;
            }
            .price {
              line-height: normal;
              color: #e02828;
              font-size: .28rem;
              width: 1.4rem;
              text-indent: -.06rem;
              span {
                font-size: .3rem;
              }
            }
            .market {
              padding-top: .03rem;
              line-height: normal;
              font-size: .24rem;
              color: #aeadad;
              width: 1.3rem;
            }
            .cart {
              width: .53rem;
              height: .53rem;
              background: url(../../assets/buy-cart-12.png) no-repeat;
              background-size: 100% 100%;
              position: absolute;
              bottom: .25rem;
              right: .1rem;
            }
          }
        }
        li:nth-child(2n) {
          margin-right: 0;
        }
      }
      .no_goods {
        height: 50%;
        padding-top: 1rem;
        color: #727171;
        font-size: .3rem;
        text-align: center;
        background: #fff;
      }
    }
  }
</style>


